import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-reporte-shell',
  templateUrl: './reporte-shell.component.html',
  styleUrls: ['./reporte-shell.component.css']
})
export class ReporteShellComponent implements OnInit {
  public isCollapsed: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  options = [
    {
      icon: 'file-search',
      title: 'Reporte por Fecha',
      url: 'asistencia-persona'
    },
    {
      icon: 'file-add',
      title: 'Reporte Año Tipo Evento',
      url: 'asistencia-sede'
    },
  ];

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }
}
