import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { FilterReporteDTO } from '../model/FilterReporteDTO';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ReportesService {

  private _path : String = 'reportes';

  constructor(private api: ApiService) { }

  public _getReporteAsistenciaFecha(data: FilterReporteDTO) {
    console.log(JSON.stringify(data));
    return this.api.post(`${this._path}/persona/evento/asistencia`, data).pipe(
      map(res => res)
    )
  }

  public _exportReporteAsistenciaFecha(data: FilterReporteDTO) {
    return this.api.post(`${this._path}/persona/evento/asistencia/export`, data).pipe(
      map(res => res)
    )
  }

  public _getReporteAnualTipoEvento(data: FilterReporteDTO) {
    return this.api.post(`${this._path}/asistencia/sede`, data).pipe(
      map(res => res)
    )
  }

  public _exportReporteAnualTipoEvento(data: FilterReporteDTO) {
    return this.api.post(`${this._path}/asistencia/sede/export`, data).pipe(
      map(res => res)
    )
  }

}
