import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ReporteAsistenciaComponent } from './reporte-asistencia/reporte-asistencia.component';
import { ReporteSedeComponent } from './reporte-sede/reporte-sede.component';
import { ReporteShellComponent } from './reporte-shell/reporte-shell.component';
import { ReporteListarComponent } from './reporte-listar/reporte-listar.component';


const routes: Routes = [{
  path: '', component: ReporteShellComponent, children: [
    { path: '', redirectTo: 'asistencia-persona'},
    { path: 'listar', component: ReporteListarComponent},
    { path: 'asistencia-persona', component: ReporteAsistenciaComponent},
    { path: 'asistencia-sede', component: ReporteSedeComponent },
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportesRoutingModule { }
