import { Component, OnInit } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ParametrosService } from '../../../services/parametros.service';
import { EventoService } from '../../../services/evento.service';
import { ReportesService } from '../../../services/reportes.service';
import { FilterReporteDTO } from '../../../model/FilterReporteDTO';

@Component({
  selector: 'app-reporte-asistencia',
  templateUrl: './reporte-asistencia.component.html',
  styleUrls: ['./reporte-asistencia.component.css']
})
export class ReporteAsistenciaComponent implements OnInit {

  procesando:boolean = false;
  listaAsistenciaReporte: Array<object> = [];
  listaTipoEvento: Array<object> = [];
  listaSedes: Array<object> = [];
  listaEventos: Array<object> = [];
  filter: FilterReporteDTO = new FilterReporteDTO();

  form: FormGroup;

  constructor(
    private message: NzMessageService,
    private router: Router,
    private formBuilder: FormBuilder,
    private parametroService: ParametrosService,
    private eventoService: EventoService,
    private reporteService: ReportesService,
  ) { }

  ngOnInit() {
    this._createForm();
    this._listarParametroTipoEvento();
    this._listarParametroSede();
    this._listarEventosAll();
  }

  private _createForm(){
    this.form = this.formBuilder.group({
      tipoEvento: [''],
      dni: [''],
      capacitacion: [''],
      sede: [''],
      fechaInicio: ['']
    });
  }

  public exportReport() {
    this.filter.tipoEvento = this.form.get('tipoEvento').value;
    this.filter.idCurso = this.form.get('capacitacion').value;
    this.filter.sede = this.form.get('sede').value;
    if(this.form.get('dni').value !== ''){
      this.filter.dni = this.form.get('dni').value;
    }
    this.reporteService._exportReporteAsistenciaFecha(this.filter).subscribe(
      res => {
        this.message.success('Datos fueron recuperados on éxito.');
      }, err => {
        this.message.error('ocurrió un error al realizar esta operación');
      });
  }

  public handleFilter() {
    this.filter.tipoEvento = this.form.get('tipoEvento').value;
    this.filter.idCurso = this.form.get('capacitacion').value;
    this.filter.sede = this.form.get('sede').value;
    if(this.form.get('dni').value !== ''){
      this.filter.dni = this.form.get('dni').value;
    }
    this.procesando = true;
    this.reporteService._getReporteAsistenciaFecha(this.filter).toPromise()
      .then(res => {
        this.listaAsistenciaReporte = res['data'] || [];
        console.log('data-> ' + JSON.stringify(res['data']));
        this.procesando = false;
        this.message.success('Datos fueron recuperados on éxito.');
      }, err => {
        this.message.error('ocurrió un error al realizar esta operación');
        this.procesando = false;
      });
  }

  public _listarParametroTipoEvento() {
    this.parametroService.getParametroByCodPadre('02').toPromise()
    .then(res => {
      this.listaTipoEvento = res['data'] || [];
      console.log('data -> ' + JSON.stringify(res['data']));
      this.procesando = false;
    }, err => {
      this.procesando = false;
      this.message.error('ocurrió un error al recuperar los tipos de eventos');
    });
  }

  public _listarParametroSede() {
    this.parametroService.getParametroByCodPadre('03').toPromise()
    .then(res => {
      this.listaSedes = res['data'] || [];
      this.procesando = false;
    }, err => {
      this.procesando = false;
      this.message.error('ocurrió un error al recuperar lista de sedes');
    });
  }

  public _listarEventosAll() {
    this.eventoService.getAll().toPromise()
      .then(
        res => {
          this.listaEventos = res['data'] || [];
          this.procesando = false;
        }, err => {
          this.procesando = false;
          this.message.error('ocurrió un error al recuperar lista de eventos');
        }
      );
  }

  public _getAsistencia() {
    this.procesando = false;
  }


}
